<template>
<div class="service">
  <router-link to="/services" class="back">Назад</router-link>
  <h1>{{ info[id-1].name }}</h1>
  <h2>Мы приглашаем вас на рыбалку к Волго-Каспийскому каналу!</h2>
  <div class="block">
    <img class="services-item__image" v-bind:src=" require('../assets/images/' + info[id-1].image) " alt="img">
    <div class="text">{{ info[id-1].description }}</div>
  </div>

  <Rent />

</div>
</template>

<script>
import axios from "axios";
import Rent from "./Rent";
export default {
  name: "Service",
  components: {Rent},
  data() {
    return {
      id: this.$route.params['id'],
      info: [],
      see: true
    }
  },
  mounted() {
    axios
        .get('./db.json')
        .then(response => (this.info = response.data));
  }
}
</script>

<style lang="scss" scoped>
.service {
  padding: 20px;
}
.back {
  display: inline-block;
  color: white;
  background-color: #910e27;
  padding: 10px;
  width: 100px;
  box-sizing: border-box;
  text-align: center;
}
.services-item__image {
  width: 500px;
  margin-right: 20px;
}
.block {
  display: flex;
  width: 1200px;
  justify-content: space-between;
  align-items: center;
}
.text {
  font-size: 30px;
  line-height: 1.2;
}
.hidden {
  display: none;
}
.show {
  display: block;
}
</style>